import React from 'react'
import Zewa from '../../img/zewa.png'
import logoGame from '../../img/logoGame.png'
import './index.scss'
import bridge from '@vkontakte/vk-bridge';


export default class Menu extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            name: 'user'
        }
        bridge.subscribe((e) => console.log(e));
        bridge.send("VKWebAppAllowNotifications");
    }
    render() {
        return (
            <section className="menu">
                <div className="row justify-content-center nav">
                    <div className="col-lg-3">
                        <img src={Zewa} alt="" />
                    </div>
                    <div className="col-lg-7">
                        <img src={logoGame} alt="" />
                    </div>
                </div>
                <div className="row justify-content-center infoData">
                    <div className="col-lg-9">
                        <p>
                            Привет, <span>{this.state.name}</span>. Играй и получай скидки до <span className="special">70%</span><br />
                            на покупку продукции Zewa в магазинах Магнит.
                            Пройди все квесты и участвуй в розыгрыше подарочного купона Магнит на <span className="special">3000 рублей</span>.
                        </p>
                    </div>
                </div>
                <div className="row justify-content-center navigation">
                    <div className="col-lg-6">
                        <div className="row justify-content-center">
                            <div className="col-md-auto">
                                <button className="selectionBtn pink">Начать игру</button>
                            </div>
                        </div>
                        <div className="row justify-content-center">
                            <div className="col-md-auto">
                                <button className="selectionBtn">Рекорды</button>
                            </div>
                        </div>
                        <div className="row justify-content-center">
                            <div className="col-md-auto">
                                <button className="selectionBtn">Правила</button>
                            </div>
                        </div>
                        <div className="row justify-content-center">
                            <div className="col-md-auto">
                                <button className="selectionBtn">Выход</button>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}